<template>
  <nav>
    <ul>
      <li :key="i" v-for="link, i in links">
        <a :class="{
          'active': (this.currentSlide == link.slide) || (this.currentSlide == link.anotherSlides)
        }" @click='animateSlideNav(link.slide, $event)' :href="link.route">{{link.label }}</a>
      </li>
     
    </ul>
  </nav>
</template>

<script>
export default {
  name: "NavBar",
  props: ['currentSlide'],
  watch: {
    currentSlide: function() {
      if(this.currentSlide == 2 || this.currentSlide == 3){
        this.links[0].active = 'active'
      }
    }
  },
  methods: {
    animateSlideNav: function(slide, event) {
      event.currentTarget.classList.add('active')
      this.$parent.animateSlideByClick(slide, event)
    }
  },
  data: () => ({
    links: [
      {
        route: '#about',
        label: 'About Us',
        active: '',
        slide: 2,
        anotherSlides: 3
      },
      {
        route: '#services',
        label: 'Services'
      },
      {
        route: '#estimate',
        label: 'Estimate'
      },
      {
        route: '#contacts',
        label: 'Contacts'
      },
    ]
  })
};
</script>

<style lang="sass" scoped>
.router-link-active
  color: $yellow
ul
  display: flex
  align-items: center
  column-gap: 81px
  text-transform: uppercase
  li
    &:hover
      a:not(.router-link-active)
        color: lighten($gray, 20%)
  a
    transition: color 0.2s ease-in-out
    transition-delay: 0.5s
    color: $gray
    font-size: 12px
    +ph
    white-space: nowrap
    letter-spacing: 0.8px
    &.active
      color: $yellow !important

      

</style>